.timeline-container {
    position: relative;
    width: 100%;
  }
  
  .tooltip {
    position: absolute;
    padding: 10px;
    background: rgba(255, 255, 255, 0.9);
    border: 1px solid #ddd;
    border-radius: 4px;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.2s;
    font-size: 14px;
    z-index: 1000;
  }
  
  .dot {
    transition: r 0.2s;
  }
  
  .dot:hover {
    r: 6;
  }