/* DataControls.css */
.data-controls {
  background-color: var(--medium-bg);
  border-radius: 12px;
  margin-bottom: 30px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
}

.controls-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: var(--light-bg);
  cursor: pointer;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.controls-header h2 {
  margin: 0;
  font-family: 'Orbitron', sans-serif;
  font-size: 1.3rem;
  color: var(--accent-color);
}

.toggle-button {
  background: none;
  border: none;
  color: var(--text-medium);
  cursor: pointer;
  font-size: 0.9rem;
  padding: 5px 10px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.toggle-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--text-light);
}

.controls-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease;
}

.data-controls.expanded .controls-content {
  max-height: 1000px;
  padding: 20px;
}

.control-section {
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.control-section:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.control-section h3 {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.1rem;
  color: var(--text-light);
}

/* Year range controls */
.year-range-control {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 15px;
  align-items: center;
}

.year-input-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

.year-input-group label {
  font-size: 0.9rem;
  color: var(--text-medium);
}

.year-input-group input {
  width: 80px;
  padding: 8px;
  background-color: var(--dark-bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: var(--text-light);
  font-size: 0.9rem;
}

.apply-button {
  padding: 8px 15px;
  background-color: var(--accent-color);
  color: var(--dark-bg);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
}

.apply-button:hover {
  background-color: var(--ufo-glow);
  transform: translateY(-2px);
}

.year-range-slider {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 0 10px;
}

.year-range-slider input[type="range"] {
  width: 100%;
  -webkit-appearance: none;
  height: 4px;
  background: linear-gradient(to right, var(--accent-color), var(--ufo-glow));
  border-radius: 2px;
}

.year-range-slider input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: var(--text-light);
  cursor: pointer;
  border: 2px solid var(--accent-color);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

/* Search and selection lists */
.search-input {
  display: flex;
  margin-bottom: 15px;
  gap: 10px;
}

.search-input input {
  flex: 1;
  padding: 10px 15px;
  background-color: var(--dark-bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: var(--text-light);
  font-size: 0.9rem;
}

.clear-button {
  padding: 0 12px;
  background-color: rgba(255, 82, 82, 0.2);
  color: #ff5252;
  border: 1px solid rgba(255, 82, 82, 0.3);
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.2s ease;
}

.clear-button:hover {
  background-color: rgba(255, 82, 82, 0.3);
}

.selection-list {
  max-height: 200px;
  overflow-y: auto;
  background-color: var(--dark-bg);
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 10px;
}

.selection-item {
  margin-bottom: 8px;
}

.selection-item label {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px;
  border-radius: 4px;
  transition: background-color 0.2s ease;
}

.selection-item label:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.selection-item input[type="checkbox"] {
  margin-right: 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border: 2px solid var(--secondary-color);
  border-radius: 3px;
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
}

.selection-item input[type="checkbox"]:checked {
  background-color: var(--accent-color);
  border-color: var(--accent-color);
}

.selection-item input[type="checkbox"]:checked::after {
  content: "✓";
  position: absolute;
  color: var(--dark-bg);
  font-size: 12px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.no-results {
  padding: 15px;
  text-align: center;
  color: var(--text-medium);
  font-style: italic;
}

/* Facility controls */
.facility-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.facility-toggle {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  background-color: var(--dark-bg);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  cursor: pointer;
  transition: all 0.2s ease;
}

.facility-toggle:hover {
  background-color: rgba(255, 255, 255, 0.05);
  transform: translateY(-2px);
}

.toggle-label {
  margin-left: 10px;
  font-weight: 500;
}

.toggle-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-left: 10px;
  border-radius: 50%;
}

.toggle-icon.military {
  background-color: rgba(220, 20, 60, 0.7);
  border: 1px solid rgba(220, 20, 60, 0.9);
}

.toggle-icon.nuclear {
  background-color: rgba(255, 165, 0, 0.7);
  border: 1px solid rgba(255, 165, 0, 0.9);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .year-range-control {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .facility-controls {
    flex-direction: column;
  }
}

/* Custom scrollbar */
.selection-list::-webkit-scrollbar {
  width: 8px;
}

.selection-list::-webkit-scrollbar-track {
  background: var(--dark-bg);
  border-radius: 4px;
}

.selection-list::-webkit-scrollbar-thumb {
  background: var(--light-bg);
  border-radius: 4px;
}

.selection-list::-webkit-scrollbar-thumb:hover {
  background: var(--secondary-color);
}
