:root {
  --primary-color: #1a237e;
  --secondary-color: #7986cb;
  --accent-color: #4fc3f7;
  --ufo-glow: #64ffda;
  --dark-bg: #121212;
  --medium-bg: #1e1e1e;
  --light-bg: #2d2d2d;
  --text-light: #f5f5f5;
  --text-medium: #b0b0b0;
  --text-dark: #333333;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--dark-bg);
  color: var(--text-light);
  font-family: 'Roboto', sans-serif;
  background-image: 
    radial-gradient(circle at 10% 20%, rgba(25, 118, 210, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 90% 80%, rgba(100, 255, 218, 0.07) 0%, transparent 50%);
  background-attachment: fixed;
}

.app {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

header {
  text-align: center;
  margin-bottom: 40px;
  padding: 30px 0;
  position: relative;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

header h1 {
  font-family: 'Orbitron', sans-serif;
  font-size: 2.5rem;
  margin-bottom: 10px;
  background: linear-gradient(to right, var(--accent-color), var(--ufo-glow));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0 0 15px rgba(100, 255, 218, 0.3);
}

header p {
  font-size: 1.2rem;
  color: var(--text-medium);
  max-width: 600px;
  margin: 0 auto;
}

.controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
  padding: 15px;
  background-color: var(--medium-bg);
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.control-group {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 15px;
  background-color: var(--light-bg);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.control-group:hover {
  background-color: rgba(79, 195, 247, 0.1);
}

.control-group label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
}

.control-group input[type="checkbox"] {
  appearance: none;
  width: 18px;
  height: 18px;
  border: 2px solid var(--secondary-color);
  border-radius: 4px;
  margin-right: 8px;
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
}

.control-group input[type="checkbox"]:checked {
  background-color: var(--accent-color);
  border-color: var(--accent-color);
}

.control-group input[type="checkbox"]:checked::after {
  content: "✓";
  position: absolute;
  color: var(--dark-bg);
  font-size: 14px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main-visualization {
  margin-bottom: 40px;
  background-color: var(--medium-bg);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.supporting-charts {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}

@media (min-width: 768px) {
  .supporting-charts {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 1200px) {
  .supporting-charts {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.chart-container {
  display: flex;
  flex-direction: column;
  background-color: var(--medium-bg);
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.chart-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.4);
}

.chart {
  padding: 20px;
  background-color: var(--medium-bg);
  border-radius: 8px 8px 0 0;
  min-height: 250px;
}

.chart-description {
  padding: 20px;
  background-color: var(--light-bg);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.chart-description h3 {
  font-family: 'Orbitron', sans-serif;
  margin-top: 0;
  color: var(--accent-color);
  font-size: 1.2rem;
}

.chart-description p {
  color: var(--text-medium);
  font-size: 0.95rem;
  line-height: 1.5;
  margin-bottom: 0;
}

.loading, .error {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  font-size: 20px;
}

.loading {
  color: var(--accent-color);
  flex-direction: column;
}

.loading:after {
  content: "";
  width: 50px;
  height: 50px;
  border: 5px solid rgba(79, 195, 247, 0.3);
  border-radius: 50%;
  border-top-color: var(--accent-color);
  animation: spin 1s ease-in-out infinite;
  margin-top: 20px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error {
  color: #ff5252;
  background-color: rgba(255, 82, 82, 0.1);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid rgba(255, 82, 82, 0.3);
}

footer {
  margin-top: 60px;
  text-align: center;
  font-size: 14px;
  color: var(--text-medium);
  padding: 20px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

/* Tooltip styling */
.tooltip {
  position: absolute;
  padding: 10px 15px;
  background-color: rgba(18, 18, 18, 0.9);
  color: var(--text-light);
  border-radius: 6px;
  pointer-events: none;
  font-size: 14px;
  z-index: 10;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  border: 1px solid var(--accent-color);
  opacity: 0;
  transition: opacity 0.2s ease;
  max-width: 250px;
}

/* Add a subtle pulsing animation to UFO points */
@keyframes pulse {
  0% { opacity: 0.6; }
  50% { opacity: 1; }
  100% { opacity: 0.6; }
}

.ufo-sighting {
  animation: pulse 3s infinite;
}