/* ProximityAnalysis.css */
.proximity-analysis-container {
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  background-color: var(--medium-bg);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.proximity-analysis-container svg {
  overflow: visible;
}

.proximity-analysis-container text {
  fill: var(--text-light);
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
}

.proximity-analysis-container .bar {
  transition: all 0.3s ease;
}

.proximity-analysis-container .bar:hover {
  filter: brightness(1.2);
}

.proximity-analysis-container .bar-label {
  font-size: 10px;
  fill: var(--text-light);
  opacity: 0.8;
}

.proximity-analysis-container .axis path,
.proximity-analysis-container .axis line {
  stroke: rgba(255, 255, 255, 0.2);
}

.proximity-analysis-container .tick text {
  fill: var(--text-medium);
  font-size: 10px;
}

/* Tooltip styling */
.tooltip {
  position: absolute;
  opacity: 0;
  background-color: var(--dark-bg);
  border: 1px solid var(--secondary-color);
  border-radius: 8px;
  padding: 10px;
  pointer-events: none;
  transition: opacity 0.2s;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  font-size: 12px;
  color: var(--text-light);
  z-index: 1000;
  max-width: 200px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .proximity-analysis-container {
    padding: 15px 10px;
  }
  
  .proximity-analysis-container text {
    font-size: 10px;
  }
}

/* Animation for bars */
@keyframes barGrow {
  from {
    height: 0;
    y: 100%;
  }
  to {
    height: attr(height px);
    y: attr(y px);
  }
}

.proximity-analysis-container .bar {
  animation: barGrow 1s ease-out;
}
